export default {
  name: "Binsert",
  data() {
    return {

    }
  },
  methods: {
    bottomBtn() {
      this.$emit('btn-click')
    },
    goToGet() {
      this.$emit('goToWhere')
    }
  },
  props: {
    leastNum: {
      type: Number,
      default: 0
    },
    type: {
      type: Number,
      default: 1
    },
    timeShow: {
      type: Boolean,
      default: true
    },
    canDo: {
      type: Boolean,
      default: true
    },
    leftTime: {
      type: String,
      default: "14:00开始"
    }
  }
}



